<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
			<el-table-column type="index" width="50">
			</el-table-column>
			<el-table-column prop="name" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="description" label="" width="250"  sortable>
			</el-table-column>
			<el-table-column prop="time" label="" width="200" sortable>
			</el-table-column>
			<el-table-column prop="isSystem" label="" width="120" :formatter="formatSystem" sortable>
			</el-table-column>
			<el-table-column  label="" width="200" sortable>
				<template slot-scope="scope">
					<img v-bind:src ="scope.row.picture" alt="" max-width="200" height="200" class="rowpicture"/>
				</template>
			</el-table-column>
			<el-table-column label="" width="450" >
				<template scope="scope">
					<el-button size="small" @click="handleView(scope.$index, scope.row)"></el-button>
					<el-button size="small" @click="handleViewTop(scope.$index, scope.row)"></el-button>
					<el-button size="small" @click="handleViewElite(scope.$index, scope.row)"></el-button>
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column> 	
		</el-table>

		<!---->
		<el-dialog title="" v-model="blogsFormVisible" :close-on-click-modal="false" style="width: 100%;">
			<el-table :data="BlogList" v-loading="blogsLoading" ref="BlogList" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="theme" label="" width="200" sortable>
				</el-table-column>
				<el-table-column prop="poster.nickName" label="" width="150" sortable>
				</el-table-column>
				<el-table-column prop="posttime" label="" width="150" sortable>
				</el-table-column>
					<el-table-column label="" width="200">
					<template scope="scope">
						<el-button size="small" @click="handleTopBlog(scope.$index, scope.row)" :loading="deleteLoading"></el-button>
						<el-button size="small" @click="handleEliteBlog(scope.$index, scope.row)" :loading="deleteLoading"></el-button>
						<el-button type="danger" size="small" @click="handleDelBlog(scope.$index, scope.row)" :loading="deleteLoading"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-dialog>

			<el-dialog title="" v-model="eliteBlogsFormVisible" :close-on-click-modal="false" style="width: 100%;">
			<el-table :data="BlogList" v-loading="blogsLoading" ref="BlogList" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="theme" label="" width="200" sortable>
				</el-table-column>
				<el-table-column prop="poster.nickName" label="" width="150" sortable>
				</el-table-column>
				<el-table-column prop="posttime" label="" width="150" sortable>
				</el-table-column>
					<el-table-column label="" width="200">
					<template scope="scope">
						<el-button type="danger" size="small" @click="handleDelBlog(scope.$index, scope.row)" :loading="deleteLoading"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-dialog>

			<el-dialog title="" v-model="topBlogsFormVisible" :close-on-click-modal="false" style="width: 100%;">
			<el-table :data="BlogList" v-loading="blogsLoading" ref="BlogList" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="theme" label="" width="200" sortable>
				</el-table-column>
				<el-table-column prop="poster.nickName" label="" width="150" sortable>
				</el-table-column>
				<el-table-column prop="posttime" label="" width="150" sortable>
				</el-table-column>
					<el-table-column label="" width="200">
					<template scope="scope">
						<el-button type="danger" size="small" @click="handleDelBlog(scope.$index, scope.row)" :loading="deleteLoading"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type='textarea' v-model="editForm.description" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
						<el-radio-group v-model="editForm.isSystem">
							<el-radio label=""></el-radio>
							<el-radio label=""></el-radio>
						</el-radio-group>
				</el-form-item>		
				
				<!----->
				<el-form-item label="()">
					<el-upload 
					class="upload-demo" 
					accept="image/jpeg,image/gif,image/png"
					:before-upload="onBeforeUpload"
					action="https://upload-z2.qiniup.com" 
					:auto-upload="true"
					:limit='1'  
					:data="addform"
					:on-success="handleSuccess"
					ref="upload" 
					:on-change='changeUpload'
					:file-list="files">
						<el-button size="small" type="primary"></el-button>
						<div slot="tip" class="el-upload__tip">jpg/png500kb</div>
					</el-upload>
				</el-form-item>
			</el-form>

			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
					<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type='textarea' v-model="addForm.description" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
						<el-radio-group v-model="addForm.isSystem">
							<el-radio label=""></el-radio>
							<el-radio label=""></el-radio>
				</el-radio-group>
				</el-form-item>		
				
				<!----->
				<el-form-item label="()">
					<el-upload 
					class="upload-demo" 
					accept="image/jpeg,image/gif,image/png"
					:before-upload="onBeforeUpload"
					action="https://upload-z2.qiniup.com" 
					:auto-upload="true" 
					:data="addform"
					:on-success="handleSuccess"
					ref="upload" 
					:limit="1"
					:on-change='changeUpload'
					:file-list="files">
						<el-button size="small" type="primary"></el-button>
						<div slot="tip" class="el-upload__tip">jpg/png500kb</div>
					</el-upload>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
import util from "../../common/js/util";
//import NProgress from 'nprogress'
import {
  getBoardList,
  removeBoard,
  editBoard,
  addBoard,
  getQiniuUploadToken,
  removeBlog,
	getBlogList,
	pushTopBlog,
	pushEliteBlog,
	getTopBlog,
	getEliteBlog,
} from "../../api/api";

export default {
  data() {
    return {
      users: [],
      srcOfImage:"",	
      listLoading: false,

      editFormVisible: false, //
      editLoading: false,
      editFormRules: {
        name: [{ required: true, message: "" }]
      },
      //
      editForm: {
        id: "",
				name: "",
				description:"",
				//time:"",
				imagesrc:"",
				isSystem: "0"
	  },

      addFormVisible: false, //
      addLoading: false,
      addFormRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, trigger: "blur" }
        ],
        description: [{ required: true, message: "", trigger: "blur" }],
      },
      //
      addForm: {
      id: "",
			name: "",
			description:"",
			//time:"",
			imagesrc: "",
			isSystem: "0",
		},
	  //
		blogsFormVisible:false,
		topBlogsFormVisible:false,
		eliteBlogsFormVisible:false,
	  blogsLoading:false,
	  BlogList:[],
	  deleteLoading:false,
    };

		//id
		boardId: "";
  },
  methods: {
    handleCurrentChange(val) {
      this.page = val;
      this.getBoards();
		},
		
		//
		formatSystem: function (row, column) {
				return row.isSystem == "1" ? '' : row.isSystem == "0" ? '' : '';
			},
    //
    getBoards: function() {
		var user = sessionStorage.getItem('user');
		user = JSON.parse(user);
       	let para = {
      };
      this.listLoading = true;
      //NProgress.start();
      getBoardList(para).then(res => {
        this.users = res.data.Boards;
				this.users.forEach((e) => {
						e.time = e.time ? (e.time.year + '-' + e.time.month + '-' + e.time.day + ' ' + e.time.hour + ':' + e.time.minutes) : ""
					})
        this.listLoading = false;
        //NProgress.done();
      });
    },
    //
    handleDel: function(index, row) {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = Object.assign({}, row);
          removeBoard({ id: para._id }).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
							message: (res.data.code == "1"	) ? "" : "",
								type: (res.data.code == "1"	) ? "success" : "error"
					});
            this.getBoards();
          });
        })
        .catch(() => {});
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
			this.editForm.id = row._id;
    },
    //
    handleAdd: function() {
      this.addFormVisible = true;
     this.$refs["addForm"].resetFields();
	},
	
	// 
	//
	handleView: function(index, row) {
		this.blogsFormVisible = true;
		this.blogsLoading = true;
		let para = {
			board:row._id,
			describe :'getPostBlogs'
		};
		this.boardId = row._id;
		getBlogList(para).then((res) => {
			this.BlogList = res.data.PostBlogs;
			this.BlogList.forEach((e) => {
						e.posttime = e.time ? (e.time.year + '-' + e.time.month + '-' + e.time.day + ' ' + e.time.hour + ':' + e.time.minutes) : ""
					})
			this.blogsLoading = false;
			//console.log(this.BlogList);
		})
	},
	//
	handleViewTop: function(index, row) {
		this.topBlogsFormVisible = true;
		this.blogsLoading = true;
		let para = {	
			board:row._id,
			describe :'getPostBlogs'
		};
		this.boardId = row._id;
		getTopBlog(para).then((res) => {
			this.BlogList = res.data.PostBlogs;
			this.BlogList.forEach((e) => {
						e.posttime = e.time ? (e.time.year + '-' + e.time.month + '-' + e.time.day + ' ' + e.time.hour + ':' + e.time.minutes) : ""
					})
			this.blogsLoading = false;
			//console.log(this.BlogList);
		})
	},
	//
	handleEliteBlog: function(index, row) {
		this.eliteBlogsFormVisible = true;
		this.blogsLoading = true;
		let para = {	
			board:row._id,
			describe :'getPostBlogs'
		};
		this.boardId = row._id;
		getEliteBlog(para).then((res) => {
			this.BlogList = res.data.PostBlogs;
			this.BlogList.forEach((e) => {
						e.posttime = e.time ? (e.time.year + '-' + e.time.month + '-' + e.time.day + ' ' + e.time.hour + ':' + e.time.minutes) : ""
					})
			this.blogsLoading = false;
			//console.log(this.BlogList);
		})
	},

    //
    editSubmit: function() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.editLoading = true;
			//NProgress.start();
						this.editForm.imagesrc = this.srcOfImage ? this.srcOfImage : "";
            let para = Object.assign({}, this.editForm);
						console.log(para);
            editBoard(para).then(res => {
              this.editLoading = false;
              //NProgress.done();
              this.$message({
								message: (res.data.code == "1"	) ? "" : "",
								type: (res.data.code == "1"	) ? "success" : "error"
						});
              this.$refs["editForm"].resetFields();
              this.editFormVisible = false;
              this.getBoards();
            });
          });
        }
      });
    },
    //
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
						//NProgress.start();
						this.addForm.imagesrc = this.srcOfImage;
						this.addForm.isSystem = this.addForm.isSystem == ''?"1" : "0";
						var user = sessionStorage.getItem('user');
							if (user) {
								user = JSON.parse(user);
								if(user.permission != "3" && this.addForm.isSystem == "1"){	
											this.addLoading = false;
											this.$message({
												message: "",
												type: "warning"
									});
											return;
								}
							}
						let para = Object.assign({}, this.addForm);

            addBoard(para).then(res => {
              this.addLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["addForm"].resetFields();
              this.addFormVisible = false;
              this.getBoards();
            });
          });
        }
      });
    },
	
	 onBeforeUpload(file) {
      const isIMAGE = file.type === "image/jpeg" || "image/gif" || "image/png";
      const isLt1M = file.size / 1024 / 1024 / 2 < 1;

      if (!isIMAGE) {
        this.$message.error("!"); 
        return;
      }
      if (!isLt1M) {
        this.$message.error("500KB!"); 
        return;
      }

      if (isIMAGE && isLt1M) {
        let param = {
          fileName: file.name
        };
        getQiniuUploadToken(param)
          .then(res => {
            if (res.data.code === "1") {
              this.addform = {
                key: res.data.result.fileName,
                token: res.data.result.uploadToken
              };
            } else {
              this.$message.error("");
            }
          })
          .catch(() => {
            this.$message.error("");
          });
      }
    },
    changeUpload: function(file, fileList) {
      //
      this.fileList = fileList;
      this.$nextTick(() => {
        let upload_list_li = document.getElementsByClassName(
          "el-upload-list"
        )[0].children;
        for (let i = 0; i < upload_list_li.length; i++) {
          let li_a = upload_list_li[i].children[0];
          let imgElement = document.createElement("img");
          imgElement.setAttribute("src", fileList[i].url);
          imgElement.setAttribute("style", "max-width:50%;padding-left:25%");
          if (li_a.lastElementChild.nodeName !== "IMG") {
            li_a.appendChild(imgElement);
          }
        }
      });
    },

    handleSuccess(response, file, fileList) {
      var imagekey = `http://pg6q6hwnq.bkt.clouddn.com/${response.key}`;
      this.srcOfImage = imagekey;
 
	},


	//
	handleDelBlog: function(index, row) {
		this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.deleteLoading = true;
					let para = {
						id: row._id,
					//	operation : "delete"
					};
					removeBlog(para).then((res) => {
						this.deleteLoading = false;
						this.$message({
								message: (res.data.code == "1"	) ? "" : "",
								type: (res.data.code == "1"	) ? "success" : "error"
							});
							let param = {
						board:this.boardId,
						describe :'getPostBlogs'
						};
						getBlogList(param).then((res) => {
							this.BlogList = res.data.PostBlogs;
							this.BlogList.forEach((e) => {
										e.posttime = e.time ? (e.time.year + '-' + e.time.month + '-' + e.time.day + ' ' + e.time.hour + ':' + e.time.minutes) : ""
									})
							//console.log(this.BlogList);
					})
					});
				}).catch(() => {

				});
		},

		//
		handleTopBlog: function(index, row) {
				this.deleteLoading = true;
					let para = {
						id: row._id,
						//operation : "top"
					};
					pushTopBlog(para).then((res) => {
						this.deleteLoading = false;
						this.$message({
								message: (res.data.code == "1"	) ? "" : "",
								type: (res.data.code == "1"	) ? "success" : "error"
							});
							let param = {
						board:this.boardId,
						describe :'getPostBlogs'
						};
						getBlogList(param).then((res) => {
							this.BlogList = res.data.PostBlogs;
							this.BlogList.forEach((e) => {
										e.posttime = e.time ? (e.time.year + '-' + e.time.month + '-' + e.time.day + ' ' + e.time.hour + ':' + e.time.minutes) : ""
									})
							//console.log(this.BlogList);
					})
					});
		},

		//
		handleEliteBlog: function(index, row) {
			this.deleteLoading = true;
					let para = {
						id: row._id,
						//operation : "elite"
					};
					pushEliteBlog(para).then((res) => {
						this.deleteLoading = false;
						this.$message({
								message: (res.data.code == "1"	) ? "" : "",
								type: (res.data.code == "1"	) ? "success" : "error"
							});
							let param = {
						board:this.boardId,
						describe :'getPostBlogs'
						};
						getBlogList(param).then((res) => {
							this.BlogList = res.data.PostBlogs;
							this.BlogList.forEach((e) => {
										e.posttime = e.time ? (e.time.year + '-' + e.time.month + '-' + e.time.day + ' ' + e.time.hour + ':' + e.time.minutes) : ""
									})
							//console.log(this.BlogList);
					})
					});
		},
  },
	mounted() {
		this.getBoards();
	}
};
</script>

<style scoped>
.rowpicture{
	    width: 100%;
      height: 100%;
      overflow: hidden;
}
</style>



